* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    background: transparent;
}

body {
    background: white;
    color: black;
    overflow: hidden;
    overflow-y: scroll;
}

h1 {
    color: red;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content {
    flex-grow: 1;
}